html {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  font-family: 'Montserrat', sans-serif;
  background-color: var(--color-white);
  color: var(--color-black);
  line-height: 1.3;
  font-size: $font-body;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100%;
}


.error {
  border: 2px #ff4019 solid;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: var(--color-orange-200);
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.error {
  border: 2px #ff4019 solid; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow-x: hidden;
}

.main {
  flex-grow: 1;
  position: relative;
}

.container,
.wrapper {
  max-width: 1140px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.wrapper {
  max-width: 1290px;
}

.btn {
  display: block;
  background-color: var(--color-black);
  color: var(--color-white);
  border-radius: $border-radius;
  padding: 15px 30px;
  font-weight: 600;
  position: relative;
  z-index: 0;
  transition: background-color 0.6s ease;

  &:hover {
    background-color: transparent;
  }

  &::before {
    background-color: var(--color-orange);
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
    border-radius: $border-radius;
    transition: all 0.4s ease;
    width: 0;
    height: 100%;
  }

  &:hover::before {
    width: 100%;
  }

  &--reverse {
    background-color: var(--color-orange);

    &:hover {
      background-color: transparent;
      color: var(--color-orange);
      border-color: var(--color-orange);
    }

    &::before {
      background-color: transparent;
    }

    &:hover::before {
      width: 100%;
    }
  }

  &.disable {
    border-color: transparent;
    background-color: transparent;
    color: var(--color-white);
  }
}

section {
  margin-bottom: 150px;

  @include mobile-l {
    margin-bottom: 100px;
  }

  @include mobile {
    margin-bottom: 70px;
  }
}

h2 {
  font-size: 42px;
  line-height: 1.2;
  font-weight: 600;
  margin-bottom: 15px;

  @include mobile-l {
    font-size: 30px;
  }

  @include mobile {
    font-size: 26px;
  }
}

h3 {
  font-weight: 600;
  font-size: 18px;
  line-height: 1.2;
  color: var(--color-black-800)
}

p,
span {
  font-size: 14px;
  line-height: 1.7;
  color: var(--color-gray-800);
  letter-spacing: 0.3px;
}

span {
  font-size: 16px;

}

.background {
  position: relative;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    border-radius: $border-radius-20px;
    bottom: 0;
    right: 0;
    background: var(--color-orange-linear);
    left: 0;
    z-index: -1;
    opacity: 0;
    transition: opacity .3s linear;
  }
}

.items {
  @include flex-all-sb;
}

.video {
  max-width: 520px;
  width: 100%;
  margin-right: 30px;
  position: relative;

  @include tablet {
    min-width: 400px;
  }

  @include mobile {
    min-width: auto;
  }
}

.play {
  position: absolute;
  width: 70px;
  height: 70px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  z-index: 1;
  overflow: hidden;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(255, 255, 255, 0.15);
    backdrop-filter: blur(5px);
    z-index: 0;
    height: 100%;
    width: 100%;
  }

  &:before {
    content: "";
    position: absolute;
    height: 50px;
    top: 50%;
    left: 50%;
    border-radius: 50%;
    transform: translate(-50%, -50%);
    width: 50px;
    background-color: var(--color-orange);
    z-index: 2;
  }

  &__icon {
    position: relative;
    max-width: 15px;
    width: 100%;
    height: 15px;
    z-index: 2;
  }
}