@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap");
@import url(./vendor/swiper-bundle.css);
:root {
  --color-black: #100d0d;
  --color-black-800: #323232;
  --color-white: #fff;
  --color-orange: #f9af31;
  --color-orange-200: #ffe4b7;
  --color-orange-100: #fef7ea;
  --color-gray-800: #777777;
  --color-gray-500: #b2b2b2;
  --color-gray-200: #f7f7f7;
  --color-gray-100: #eaeaea;
  --color-disabled: #d8d8d8;
  --color-red: #cf1515;
  --color-green: #269c47;
  --color-orange-linear: linear-gradient(278.62deg, #fce4bb 1.35%, #fef7ea 113.81%);
  --color-border: rgba(249, 175, 49, 0.3);
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  outline: var(--color-orange);
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul {
  list-style: none;
}

img {
  width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: inherit;
}

button {
  cursor: pointer;
  border: 1px solid transparent;
  background-color: transparent;
}

address {
  font-style: normal;
}

html {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  font-family: 'Montserrat', sans-serif;
  background-color: var(--color-white);
  color: var(--color-black);
  line-height: 1.3;
  font-size: 14px;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100%;
}

.error {
  border: 2px #ff4019 solid;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: var(--color-orange-200);
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.error {
  border: 2px #ff4019 solid;
}

.site-container {
  overflow-x: hidden;
}

.main {
  flex-grow: 1;
  position: relative;
}

.container,
.wrapper {
  max-width: 1140px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.wrapper {
  max-width: 1290px;
}

.btn {
  display: block;
  background-color: var(--color-black);
  color: var(--color-white);
  border-radius: 5px;
  padding: 15px 30px;
  font-weight: 600;
  position: relative;
  z-index: 0;
  transition: background-color 0.6s ease;
}

.btn:hover {
  background-color: transparent;
}

.btn::before {
  background-color: var(--color-orange);
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
  border-radius: 5px;
  transition: all 0.4s ease;
  width: 0;
  height: 100%;
}

.btn:hover::before {
  width: 100%;
}

.btn--reverse {
  background-color: var(--color-orange);
}

.btn--reverse:hover {
  background-color: transparent;
  color: var(--color-orange);
  border-color: var(--color-orange);
}

.btn--reverse::before {
  background-color: transparent;
}

.btn--reverse:hover::before {
  width: 100%;
}

.btn.disable {
  border-color: transparent;
  background-color: transparent;
  color: var(--color-white);
}

section {
  margin-bottom: 150px;
}

@media (max-width: 768px) {
  section {
    margin-bottom: 100px;
  }
}

@media (max-width: 576px) {
  section {
    margin-bottom: 70px;
  }
}

h2 {
  font-size: 42px;
  line-height: 1.2;
  font-weight: 600;
  margin-bottom: 15px;
}

@media (max-width: 768px) {
  h2 {
    font-size: 30px;
  }
}

@media (max-width: 576px) {
  h2 {
    font-size: 26px;
  }
}

h3 {
  font-weight: 600;
  font-size: 18px;
  line-height: 1.2;
  color: var(--color-black-800);
}

p,
span {
  font-size: 14px;
  line-height: 1.7;
  color: var(--color-gray-800);
  letter-spacing: 0.3px;
}

span {
  font-size: 16px;
}

.background {
  position: relative;
}

.background:before {
  content: "";
  position: absolute;
  top: 0;
  border-radius: 20px;
  bottom: 0;
  right: 0;
  background: var(--color-orange-linear);
  left: 0;
  z-index: -1;
  opacity: 0;
  transition: opacity .3s linear;
}

.items {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.video {
  max-width: 520px;
  width: 100%;
  margin-right: 30px;
  position: relative;
}

@media (max-width: 1200px) {
  .video {
    min-width: 400px;
  }
}

@media (max-width: 576px) {
  .video {
    min-width: auto;
  }
}

.play {
  position: absolute;
  width: 70px;
  height: 70px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  z-index: 1;
  overflow: hidden;
}

.play::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(5px);
  z-index: 0;
  height: 100%;
  width: 100%;
}

.play:before {
  content: "";
  position: absolute;
  height: 50px;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  width: 50px;
  background-color: var(--color-orange);
  z-index: 2;
}

.play__icon {
  position: relative;
  max-width: 15px;
  width: 100%;
  height: 15px;
  z-index: 2;
}

.header {
  border-bottom: 1px solid var(--color-border);
  padding: 20px 0;
  position: relative;
}

.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 768px) {
  .header__list.nav-list {
    position: absolute;
    right: -30px;
    display: block;
    top: 90px;
    background-color: var(--color-white);
    z-index: 10;
    max-width: 300px;
    padding: 20px 0;
    width: 100%;
    transform: translateX(100%);
    transition: transform 0.3s ease;
  }
}

.header__list.nav-list.open {
  transform: translateX(0);
}

@media (max-width: 768px) {
  .header__list .nav-list__item:not(:last-child) {
    margin-bottom: 20px;
  }
}

@media (max-width: 768px) {
  .header__list .nav-list__link {
    padding: 10px;
    display: block;
  }
  .header__list .nav-list__link:before {
    display: none;
  }
}

.nav-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: auto;
  margin-right: 30px;
}

.nav-list__menu {
  display: none;
}

@media (max-width: 768px) {
  .nav-list__menu {
    display: block;
  }
}

.nav-list__item {
  font-weight: 600;
}

@media (max-width: 768px) {
  .nav-list__item {
    text-align: center;
  }
}

.nav-list__item:not(:last-child) {
  margin-right: 50px;
}

@media (max-width: 1200px) {
  .nav-list__item:not(:last-child) {
    margin-right: 30px;
  }
}

@media (max-width: 768px) {
  .nav-list__item:not(:last-child) {
    margin-right: 0;
  }
}

.nav-list__link {
  position: relative;
  transition: color 0.3s linear;
}

.nav-list__link:hover {
  color: var(--color-orange);
}

.nav-list__link:hover:before {
  opacity: 1;
  transform: translateY(0);
  visibility: visible;
}

.nav-list__link:before {
  content: "";
  position: absolute;
  width: 30px;
  height: 5px;
  background-color: var(--color-orange);
  bottom: -38px;
  border-radius: 10px 10px 0 0;
  margin: 0 auto;
  transform: translateY(10px);
  left: 0;
  right: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s linear, visibility 0.3s linear, transform 0.2s linear;
}

@media (max-width: 768px) {
  .nav-list__btn {
    margin-left: auto;
    margin-right: 30px;
  }
}

@media (max-width: 475px) {
  .nav-list__btn {
    margin: 0 15px;
    padding: 15px 5px;
  }
}

.nav-list__menu {
  max-width: 60px;
  padding: 15px 5px;
}

.banner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 0 0 20px 20px;
  background-color: var(--color-orange-100);
  padding: 80px 0 30px 80px;
  position: relative;
  align-items: flex-start;
}

@media (max-width: 1200px) {
  .banner {
    padding: 40px;
  }
}

@media (max-width: 576px) {
  .banner {
    padding: 20px;
  }
}

.banner__content {
  max-width: 380px;
  width: 100%;
  padding-top: 50px;
}

.banner__content h2 {
  color: var(--color-orange);
}

.banner__content h2 span {
  display: inline-block;
  color: var(--color-black);
  font-size: inherit;
  padding: 0;
}

.banner__content p {
  margin-bottom: 65px;
}

.banner__content span {
  padding-left: 15px;
}

.banner__content:after, .banner__content:before {
  content: "";
  position: absolute;
  background-color: var(--color-orange);
  border-radius: 5px 5px 0 0;
  width: 2px;
  left: 80px;
  bottom: 0;
}

@media (max-width: 1200px) {
  .banner__content:after, .banner__content:before {
    left: 40px;
  }
}

.banner__content:after {
  height: 80px;
  background-color: var(--color-orange);
  opacity: 0.2;
}

.banner__content:before {
  height: 40px;
}

.banner__content--last {
  padding: 0;
  max-width: 615px;
}

.banner__content--last h2 {
  font-size: 42px;
  color: var(--color-black);
}

.banner__content--last p {
  max-width: 420px;
  margin-bottom: 15px;
}

.banner__content--last .btn {
  margin-bottom: 70px;
}

.banner__content--last + .banner__picture {
  position: absolute;
  right: 0;
  bottom: 0;
}

.banner__picture {
  max-width: 790px;
}

@media (max-width: 1200px) {
  .banner__picture {
    position: absolute;
    right: 0;
    z-index: -1;
  }
}

@media (max-width: 1200px) {
  .banner__picture {
    display: none;
  }
}

.banner__problem {
  max-width: 570px;
  padding-top: 0;
}

.banner__problem h2 {
  font-size: 24px;
  color: inherit;
  margin-bottom: 25px;
}

.banner__problem p {
  font-size: 18px;
  color: inherit;
  margin-bottom: 0;
  padding-left: 75px;
  position: relative;
  min-height: 50px;
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.banner__problem p:last-of-type {
  margin-bottom: 50px;
}

@media (max-width: 768px) {
  .banner__problem p:last-of-type {
    margin-bottom: 20px;
  }
}

.banner__problem p:before, .banner__problem p:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
}

.banner__problem p:before {
  height: 50px;
  width: 50px;
  background-color: var(--color-white);
  border-radius: 20px;
}

.banner__problem p:after {
  background-image: url("../img/sprite.svg#double-arrow-red");
  height: 24px;
  width: 24px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transform: translate(50%, 50%);
}

.banner__problem + .banner__picture {
  margin: 0 auto;
}

.banner__problem--colored {
  color: var(--color-red);
}

.banner__problem--colored + .banner__picture {
  transform: translateY(-15%);
}

.banner__problem--video {
  max-width: 640px;
}

.banner__problem--video p:last-of-type {
  margin-bottom: 30px;
}

@media (max-width: 768px) {
  .banner__problem--video p:last-of-type {
    margin-bottom: 10px;
  }
}

.banner__problem--video p:after {
  background-image: url("../img/sprite.svg#double-arrow");
  transform: translate(50%, 50%);
}

.banner__problem--video + .banner__picture {
  position: relative;
}

@media (max-width: 1200px) {
  .banner__problem--video + .banner__picture {
    position: absolute;
    right: 0;
    z-index: -1;
  }
}

.banner__problem--video + .banner__picture:before {
  content: "";
  position: absolute;
  top: 0;
  height: 5px;
  background-color: var(--color-orange-100);
  width: 100%;
}

.items li {
  position: relative;
  max-width: 350px;
  width: 100%;
  border: 1px solid var(--color-gray-100);
  border-radius: 20px;
  background-color: var(--color-gray-200);
  padding: 35px;
  position: relative;
  z-index: 0;
  cursor: default;
}

@media (max-width: 1200px) {
  .items li {
    padding: 20px;
  }
}

@media (max-width: 768px) {
  .items li {
    margin: 0 auto 30px;
  }
}

@media (max-width: 576px) {
  .items li:last-child {
    margin-bottom: 0;
  }
}

.items li:not(:last-child) {
  margin-right: 20px;
}

@media (max-width: 768px) {
  .items li:not(:last-child) {
    margin-right: auto;
  }
}

.items li a {
  display: block;
  position: relative;
  padding-left: 30px;
  letter-spacing: 0.2px;
  font-size: 14px;
  line-height: 1.7;
  font-weight: 600;
  transform: translateX(0);
  transition: transform 0.3s linear, color 0.3s linear;
}

.items li a:hover {
  transform: translateX(10px);
  color: var(--color-orange);
}

.items li a:hover:before {
  background-color: currentColor;
}

.items li a:before {
  content: "";
  position: absolute;
  mask-image: url("../img/sprite.svg#arrow-right-black");
  background-color: var(--color-black);
  fill: violet;
  stroke: violet;
  height: 24px;
  width: 24px;
  left: 0;
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: cover;
}

.items li:hover {
  border: 1px solid var(--color-border);
}

.items li:hover::before {
  opacity: 1;
}

.items li:hover img {
  background-color: var(--color-orange);
}

.items__box {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 25px;
}

.items__box img {
  max-width: 45px;
  padding: 10px;
  background-color: var(--color-black);
  margin-right: 15px;
  transition: background-color 0.3s linear;
  border-radius: 5px;
}

.numbers {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 60px;
}

@media (max-width: 768px) {
  .numbers {
    order: 4;
    margin-bottom: 0;
  }
}

@media (max-width: 1200px) {
  .numbers {
    display: block;
  }
}

@media (max-width: 768px) {
  .numbers {
    display: flex;
  }
}

@media (max-width: 475px) {
  .numbers {
    flex-wrap: wrap;
    order: 3;
    margin-bottom: 30px;
  }
}

.numbers li {
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  color: var(--color-black);
  position: relative;
}

@media (max-width: 1200px) {
  .numbers li {
    margin-bottom: 20px;
    max-width: 150px;
    text-align: center;
    width: 100%;
  }
}

@media (max-width: 768px) {
  .numbers li {
    max-width: 100%;
  }
}

.numbers li:not(:last-child) {
  margin-right: 90px;
}

@media (max-width: 1200px) {
  .numbers li:not(:last-child) {
    margin-right: 30px;
  }
}

@media (max-width: 576px) {
  .numbers li:not(:last-child) {
    margin-right: 0;
  }
}

.numbers li:not(:last-child):before {
  content: "";
  position: absolute;
  width: 1px;
  right: -45px;
  top: 10px;
  height: 80px;
  background-color: var(--color-gray-100);
}

@media (max-width: 1200px) {
  .numbers li:not(:last-child):before {
    right: -30px;
  }
}

@media (max-width: 1200px) {
  .numbers li:not(:last-child):before {
    height: 1px;
    width: 100%;
    top: auto;
    bottom: 0;
    left: 0;
  }
}

@media (max-width: 768px) {
  .numbers li:not(:last-child):before {
    display: none;
  }
}

@media (max-width: 1200px) {
  .numbers li:nth-child(2n) {
    margin: 0 auto 20px;
  }
}

@media (max-width: 1200px) {
  .numbers li:last-child {
    margin-left: auto;
  }
}

.numbers li span {
  display: block;
  font-size: 44px;
  line-height: 1.2;
  color: var(--color-orange);
  margin-bottom: 20px;
}

@media (max-width: 576px) {
  .numbers li span {
    font-size: 34px;
  }
}

@media (max-width: 475px) {
  .numbers li span {
    font-size: 28px;
  }
}

.contact-form {
  max-width: 460px;
  width: 100%;
  z-index: 5;
}

.contact-form p {
  margin-bottom: 40px;
}

@media (max-width: 768px) {
  .contact-form {
    max-width: 100%;
  }
}

.form {
  width: 100%;
}

.form__area {
  width: 100%;
  margin-bottom: 15px;
  padding: 15px;
  border-radius: 10px;
  border: 1px solid var(--color-gray-100);
  font-size: 14px;
  line-height: 1.7;
  color: var(--color-gray-800);
}

.form__area--text {
  min-height: 150px;
  resize: none;
}

.form__btn {
  width: 100%;
}

.article {
  max-width: 350px;
  width: 100%;
  cursor: pointer;
  position: relative;
}

@media (max-width: 576px) {
  .article {
    margin: 0 auto;
  }
}

.article:hover h3 {
  color: var(--color-orange);
}

.article img {
  border-radius: 20px;
  margin-bottom: 25px;
}

.article h3 {
  margin-bottom: 15px;
  max-width: 320px;
  width: 100%;
  transition: all 0.3s ease;
}

.article a:before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
}

.filter {
  display: flex;
  align-items: center;
  margin-bottom: 40px;
}

.filter__item:not(:last-child) {
  margin-right: 15px;
}

@media (max-width: 475px) {
  .filter__item:not(:last-child) {
    margin-right: 5px;
  }
}

.filter__item {
  font-weight: 600;
  font-size: 14px;
  text-transform: capitalize;
  letter-spacing: 0.03em;
  color: var(--color-gray-500);
  border-radius: 5px;
}

.filter__item--active {
  background-color: var(--color-orange);
  color: var(--color-white);
}

.filter__link {
  padding: 8px 10px;
  display: block;
}

@media (max-width: 475px) {
  .filter__link {
    padding: 5px;
  }
}

.pagination {
  display: flex;
  align-items: center;
  margin-bottom: 150px;
}

.pagination__item:not(:last-child) {
  margin-right: 15px;
}

.pagination__item {
  font-weight: 600;
  font-size: 14px;
  line-height: 1.4;
  text-transform: capitalize;
  letter-spacing: 0.03em;
  color: var(--color-gray-500);
  border-radius: 5px;
  max-width: 44px;
  width: 100%;
  text-align: center;
}

.pagination__item--active {
  background-color: var(--color-orange);
  color: var(--color-white);
}

.pagination__item--active .pagination__icon {
  stroke: #fff;
}

.pagination__link {
  padding: 12px 10px;
  display: block;
}

.pagination__icon {
  max-width: 8px;
  width: 100%;
  height: 12px;
  fill: transparent;
  stroke: currentColor;
  stroke-width: 2px;
}

.pagination__icon--next {
  transform: rotate(180deg);
}

.single {
  margin-top: 60px;
}

.single-article h1,
.single-article h2,
.single-article p,
.single-article span {
  max-width: 600px;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 25px;
}

.single-article h1 {
  font-weight: 600;
  font-size: 42px;
  line-height: 1.4;
}

.single-article p span {
  font-weight: 600;
  font-size: 16px;
}

.single-article ul {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 40px;
}

.single-article ul li:not(:last-child) {
  margin-right: 15px;
}

.single-article img {
  margin-bottom: 40px;
}

.story {
  overflow: hidden;
  background-color: var(--color-orange-100);
}

.story__item {
  height: 530px !important;
