.process {
  text-align: center;


  &>p {
    max-width: 330px;
    margin: 0 auto 100px;
    width: 100%;
  }

  li {
    text-align: center;
    position: relative;
    padding-top: 55px;

    a {
      &:before {
        display: none;
      }
    }
  }

  &__list {
    @include tablet {
      flex-wrap: wrap;
      justify-content: center;
    }

    li {
      @include desktop {
        padding: 60px 10px 10px;
      }

      @include tablet {
        margin-bottom: 30px;
      }
    }
  }

  .items {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      mask-image: url('../img/sprite.svg#wawe');
      background-color: var(--color-gray-100);
      width: 100%;
      height: 330px;
      mask-position: center;
      mask-repeat: no-repeat;
      mask-size: cover;
    }

    &__box {
      display: block;


      img {
        max-width: 60px;
        height: 60px;
        margin: 0 auto;
        position: absolute;
        top: -20px;
        left: 0;
        right: 0;
      }
    }

    p {
      padding: 0 15px;
      min-height: 70px;

      @include desktop {
        padding: 0;
      }
    }
  }
}